<template><div><h1 id="spring-boot-详解" tabindex="-1"><a class="header-anchor" href="#spring-boot-详解"><span>Spring Boot 详解</span></a></h1>
<h2 id="_1-spring-boot-简介" tabindex="-1"><a class="header-anchor" href="#_1-spring-boot-简介"><span>1. Spring Boot 简介</span></a></h2>
<p>Spring Boot 是一个基于 Spring 框架的快速开发平台，它简化了 Spring 应用的初始搭建和开发过程。通过自动配置和起步依赖，开发者可以快速构建生产级别的应用。</p>
<h3 id="_1-1-主要特点" tabindex="-1"><a class="header-anchor" href="#_1-1-主要特点"><span>1.1 主要特点</span></a></h3>
<ul>
<li>自动配置：根据项目依赖自动配置 Spring 应用</li>
<li>起步依赖：简化依赖管理，提供常用功能的依赖组合</li>
<li>内嵌服务器：无需部署 WAR 文件</li>
<li>生产级特性：提供监控、指标、健康检查等功能</li>
<li>零配置：无需 XML 配置</li>
</ul>
